import * as React from "react";
import { INSGridRendererComponent, INSGridEditorCustomComponentSetting } from 'nscomponentsreact';
import { NSMessageBoxReact, INSMessageBoxReactSettings } from 'nscomponentsreact';
import { INSMessageBoxCustomSetting } from 'nscomponentsreact';
import {BaseComponent} from '../../base/baseComponent';
import {PetComponent} from '../../messageBox/component/petComponent';

export class PetComponentRenderer extends BaseComponent implements INSGridRendererComponent
{
    private element: any;
    private setting: any = null;
    private orignalValue: any = null;
    private currentValue: any = null;
    private objImg: any = null;
    private modal: NSMessageBoxReact;

    constructor(public props: any, public state: any) 
    {
        super(props,state);
        this.setting = props;
        const modalSetting: INSMessageBoxReactSettings = {enableModalCloseOnEscape: true,enableCloseIconVisibility: true};
        this.objImg = {dog:{url:"https://images.pexels.com/photos/1805164/pexels-photo-1805164.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500"},
                       cat: {url:"https://images.pexels.com/photos/1170986/pexels-photo-1170986.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500"},
                       goat: {url:"https://images.pexels.com/photos/58914/pexels-photo-58914.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500"},
                       sheep: {url:"https://images.pexels.com/photos/678448/pexels-photo-678448.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500"},
                       horse: {url:"https://images.pexels.com/photos/2667187/pexels-photo-2667187.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500"},
                       none: {url:"data:image/jpeg;base64,"},
                      };
        this.state = {"value": null,modalSetting: modalSetting};
    }
    
    componentDidMount() 
    {
        //console.log(this.context);
    }
    
    setData(data: any,dataField: string,index: number,colIndex: number,row: any): void
    { 
        let item = data;
        this.orignalValue = item["pet"];
        this.currentValue = this.orignalValue;
        this.setState({"value": this.currentValue,url: ""});
        //console.log(this.context);
    };
    
    getElement(): any
    {
        return this.element;
    };
        
   render() 
   {
       const style = {width: "100%"};
       const modalSetting: INSMessageBoxReactSettings = this.state.modalSetting;
       const anchorStyle: any = {cursor: "pointer"};
       return (
            <div>
                <div id="divImage" ref={(ref) => this.element = ref} style={style}>
                    <span>{this.state.value}</span> &nbsp;&nbsp;
                    <a style={anchorStyle} onClick={this.viewHandler.bind(this)}>View</a>
               </div>
               <NSMessageBoxReact setting={modalSetting} ref={(ref) => this.modal = ref} />
            </div>
       )
   }

   private viewHandler(event: any): void
   {
       const self : any = this;
       let modalSetting: INSMessageBoxCustomSetting = {titleHtml: "View " + this.state.value,bodyComponent: PetComponent,
               panelSetting:{width:"60%",height:"60%"},
               bodyComponentData: {name: this.state.value,url: this.objImg[this.state.value.toLowerCase()].url},
               buttons:[{label: "Close", callback: function(event: any)
               {
                   self.modal.close();
                   event.stopPropagation();
               }
         }]};
         this.modal.custom(modalSetting);
   }
}
