import * as React from "react";

import {BaseComponent} from '../base/baseComponent';
import {NSDatePickerReact, INSDatePickerReactSetting, INSDatePickerCustomClass} from 'nscomponentsreact';

export class DatePicker extends BaseComponent
{
    private calSimple: NSDatePickerReact;
    private calMinMax: NSDatePickerReact;
    private calDisabled: NSDatePickerReact;
    private calDisabledTextBox: NSDatePickerReact;
    private calCustomButton: NSDatePickerReact;
    private calFooter: NSDatePickerReact;
    private calFooterTimeZone: NSDatePickerReact;

    private arrTimezone: any[] = [
                   { name: "(GMT -12:00) Eniwetok, Kwajalein", value: "-12:00"},
                   { name: "(GMT -11:00) Midway Island, Samoa", value: "-11:00"},
                   { name: "(GMT -10:00) Hawaii", value: "-10:00"},
                   { name: "(GMT -9:30) Taiohae", value: "-09:50"},
                   { name: "(GMT -9:00) Alaska", value: "-09:00"},
                   { name: "(GMT -8:00) Pacific Time (US &amp; Canada)", value: "-08:00"},
                   { name: "(GMT -7:00) Mountain Time (US &amp; Canada)", value: "-07:00"},
                   { name: "(GMT -6:00) Central Time (US &amp; Canada), Mexico City", value: "-06:00"},
                   { name: "(GMT -5:00) Eastern Time (US &amp; Canada), Bogota, Lima", value: "-05:00"},
                   { name: "(GMT -4:30) Caracas", value: "-04:50"},
                   { name: "(GMT -4:00) Atlantic Time (Canada), Caracas, La Paz", value: "-04:00"},
                   { name: "(GMT -3:30) Newfoundland", value: "-03:50"},
                   { name: "(GMT -3:00) Brazil, Buenos Aires, Georgetown", value: "-03:00"},
                   { name: "(GMT -2:00) Mid-Atlantic", value: "-02:00"},
                   { name: "(GMT -1:00) Azores, Cape Verde Islands", value: "-01:00", selected:true},
                   { name: "(GMT) Western Europe Time, London, Lisbon, Casablanca", value: "+00:00"},
                   { name: "(GMT +1:00) Brussels, Copenhagen, Madrid, Paris", value: "+01:00"},
                   { name: "(GMT +2:00) Kaliningrad, South Africa", value: "+02:00"},
                   { name: "(GMT +3:00) Baghdad, Riyadh, Moscow, St. Petersburg", value: "+03:00"},
                   { name: "(GMT +3:30) Tehran", value: "+03:50"},
                   { name: "(GMT +4:00) Abu Dhabi, Muscat, Baku, Tbilisi", value: "+04:00"},
                   { name: "(GMT +4:30) Kabul", value: "+04:50"},
                   { name: "(GMT +5:00) Ekaterinburg, Islamabad, Karachi, Tashkent", value: "+05:00"},
                   { name: "(GMT +5:30) Bombay, Calcutta, Madras, New Delhi", value: "+05:50"},
                   { name: "(GMT +5:45) Kathmandu, Pokhara", value: "+05:75"},
                   { name: "(GMT +6:00) Almaty, Dhaka, Colombo", value: "+06:00"},
                   { name: "(GMT +6:30) Yangon, Mandalay", value: "+06:50"},
                   { name: "(GMT +7:00) Bangkok, Hanoi, Jakarta", value: "+07:00"},
                   { name: "(GMT +8:00) Beijing, Perth, Singapore, Hong Kong", value: "+08:00"},
                   { name: "(GMT +8:45) Eucla", value: "+08:75"},
                   { name: "(GMT +9:00) Tokyo, Seoul, Osaka, Sapporo, Yakutsk", value: "+09:00"},
                   { name: "(GMT +9:30) Adelaide, Darwin", value: "+09:50"},
                   { name: "(GMT +10:00) Eastern Australia, Guam, Vladivostok", value: "+10:00"},
                   { name: "(GMT +10:30) Lord Howe Island", value: "+10:50"},
                   { name: "(GMT +11:00) Magadan, Solomon Islands, New Caledonia", value: "+11:00"},
                   { name: "(GMT +11:30) Norfolk Island", value: "+11:50"},
                   { name: "(GMT +12:00) Auckland, Wellington, Fiji, Kamchatka", value: "+12:00"},
                   { name: "(GMT +12:45) Chatham Islands", value: "+12:75"},
                   { name: "(GMT +13:00) Apia, Nukualofa", value: "+13:00"},
                   { name: "(GMT +14:00) Line Islands, Tokelau", value: "+14:00"}
               ];

    constructor(public props: any, public state: any) 
    {        
        super(props,state);
        this.state = {
             theme: null,
        };
    
    }
    
    componentDidMount() 
    {     
    }
    
    public themeChanged(event: any)
    {
        if(this.state)
        {
            this.state.theme = event.details.selectedTheme;    
        }
    }
    
    render() 
    {
        let cellStyle: any = {display: "inline-block",width:"33%"};
        let conStyle: any = {paddingLeft: "25px",paddingTop: "50px"};
        let customClass: INSDatePickerCustomClass = {textInput: "datePickerTextBox"};
        let setting: INSDatePickerReactSetting = {customClass: customClass};
        let minMaxsetting: INSDatePickerReactSetting = {inputDateFormat:"MM/dd/yyyy",minDate:"01/01/2021",maxDate:"12/31/2021",selectedDate:new Date(),customClass: customClass};
        let disabledSetting: INSDatePickerReactSetting = {markDayDisabled: this.disabledDay.bind(this),customClass: customClass};
        let disabledTextBoxSetting: INSDatePickerReactSetting = {enableTextBoxDisabled: true,customClass: customClass};
        let customButtonSetting: INSDatePickerReactSetting = {buttonHtml:"<i class='glyphicon glyphicon-calendar'></i>",placeHolder:"yyyy-mm-dd",dateOutputFormat:"yyyy-MM-dd",customClass: customClass};
        let settingFooter: INSDatePickerReactSetting = {showFooter:true,footerContent:this.getFooter(), customClass: customClass};
        let timezoneSetting: INSDatePickerReactSetting = {showFooter:true,footerContent:this.getTimezone(),calendarWidth:300, customClass: customClass};
        return (
             <div className="box box-ns-red">
                  <div className="box-header">
                        <h3 className="box-title">DatePicker Demo</h3>
                  </div>
                  <div className="box-body">
                        <div className="row" style={conStyle}>
                            <div style={cellStyle}>
                                <p> Basic Date Picker</p>
                                <NSDatePickerReact setting={setting} ref={(ref) => this.calSimple = ref} onDateSelected={this.dateSelected.bind(this,"simple","divSimpleCalSelectedDate")}>
                                </NSDatePickerReact>
                                <div id="divSimpleCalSelectedDate" style={{height:"60px"}}>
                                &nbsp;
                                </div>
                            </div>
                            <div style={cellStyle}>
                                <p>Date Picker with Min Max date(minDate = "01/01/2021" and maxDate = "12/31/2021")</p>
                                <NSDatePickerReact setting={minMaxsetting} ref={(ref) => this.calMinMax = ref} onDateSelected={this.dateSelected.bind(this,"minmax","divCalMinMaxSelectedDate")}>
                                </NSDatePickerReact>
                                <div id="divCalMinMaxSelectedDate" style={{height:"60px"}}>
                                &nbsp;
                                </div>
                            </div>
                            <div style={cellStyle}>
                                <p>Calendar with Disabled Date (date less than today is disabled)</p>
                                <NSDatePickerReact setting={disabledSetting} ref={(ref) => this.calDisabled = ref} onDateSelected={this.dateSelected.bind(this,"disabled","divCalDisabledSelectedDate")}>
                                </NSDatePickerReact>
                                <div id="divCalDisabledSelectedDate" style={{height:"60px"}}>
                                &nbsp;
                                </div>
                            </div>
                        </div>
                        <div className="row" style={conStyle}>
                            <div style={cellStyle}>
                                <p>Date Picker with Disabled Textbox</p>
                                <NSDatePickerReact setting={disabledTextBoxSetting} ref={(ref) => this.calDisabledTextBox = ref} onDateSelected={this.dateSelected.bind(this,"disabledtextbox","divDisabledTextBoxSelectedDate")}>
                                </NSDatePickerReact>
                                <div id="divDisabledTextBoxSelectedDate" style={{height:"60px"}}>
                                &nbsp;
                                </div>
                            </div>
                            <div style={cellStyle}>
                                <p>Date Picker with Custom Button</p>
                                <NSDatePickerReact setting={customButtonSetting} ref={(ref) => this.calCustomButton = ref} onDateSelected={this.dateSelected.bind(this,"custombutton","divCustomButtonSelectedDate")}>
                                </NSDatePickerReact>
                                <div id="divCustomButtonSelectedDate" style={{height:"60px"}}>
                                &nbsp;
                                </div>
                            </div>
                            <div style={cellStyle}>
                                <p>Calendar With Footer</p>
                                <NSDatePickerReact setting={settingFooter} ref={(ref) => this.calFooter = ref} onDateSelected={this.dateSelected.bind(this,"footer","divFooterCalSelectedDate")}>
                                </NSDatePickerReact>
                                <div id="divFooterCalSelectedDate" style={{height:"60px"}}>
                                &nbsp;
                                </div>
                            </div>
                        </div>
                        <div className="row" style={conStyle}>
                            <div style={cellStyle}>
                                <p>Date Picker With Footer and Timezone</p>
                                <NSDatePickerReact setting={timezoneSetting} ref={(ref) => this.calFooterTimeZone = ref} onDateSelected={this.dateSelected.bind(this,"footertimezone","divFooterCalTimezoneSelectedDate")}>
                                </NSDatePickerReact>
                                <div id="divFooterCalTimezoneSelectedDate" style={{height:"60px"}}>
                                &nbsp;
                                </div>
                            </div>
                        </div>
                  </div>
             </div>
               
        )
    }
    
    dateSelected(type: string,destID: any,event: any)
    {
        let div: any = document.querySelector("#" + destID);
        if(div)
        {
            div.innerHTML = event.detail;
        }
        /*switch(type)
        {
            case "simple":
                break;
        }*/
    }
    
    disabledDay(date: any): boolean
    {
        let todayDate: any = new Date();
        if(date < todayDate)
        {
            return false;
        }
        return true;
    };
    
    getFooter(): any
    {
        var divFooter = document.createElement("div");
        divFooter.setAttribute("class","calFooter");
        var span = document.createElement("span");
        span.setAttribute("class","btn-group pull-left");
        var btnToday = document.createElement("button");
        btnToday.setAttribute("class","btn btn-sm btn-info");
        let self:any = this;
        btnToday.onclick = function(){
            self.calFooter.setTodayDate();
        };
        /*btnToday.addEventListener("click",function(){
            nsDatePicker.setTodayDate();
        });*/
        btnToday.innerHTML = "Today";
        span.appendChild(btnToday);
        var btnClear = document.createElement("button");
        btnClear.setAttribute("class","btn btn-sm btn-danger");
        btnClear.addEventListener("click",function(){
            self.calFooter.reset();
        });
        btnClear.innerHTML = "Clear";
        span.appendChild(btnClear);
        var btnClose = document.createElement("button");
        btnClose.setAttribute("class","btn btn-sm btn-success pull-right");
        btnClose.addEventListener("click",function(){
            self.calFooter.closeCalendar();
        });
        btnClose.innerHTML = "Close";
        divFooter.appendChild(span);
        divFooter.appendChild(btnClose);
        
        return divFooter;
    }
    
    getTimezone(): any
    {
        var divFooter = document.createElement("div");
        divFooter.setAttribute("class","calFooter");
        var span = document.createElement("span");
        span.setAttribute("class","btn-group pull-left");
        var btnToday = document.createElement("button");
        btnToday.setAttribute("class","btn btn-sm btn-info");
        let self: any = this;
        btnToday.onclick = function(){
            self.calFooterTimeZone.setTodayDate();
        };
        /*btnToday.addEventListener("click",function(){
            nsDatePicker.setTodayDate();
        });*/
        btnToday.innerHTML = "Today";
        span.appendChild(btnToday);
        var btnClear = document.createElement("button");
        btnClear.setAttribute("class","btn btn-sm btn-danger");
        btnClear.addEventListener("click",function(){
            self.calFooterTimeZone.reset();
        });
        btnClear.innerHTML = "Clear";
        span.appendChild(btnClear);
        var select = document.createElement("select");
        select.style.width = "250px";
        for (var count = 0; count < this.arrTimezone.length; count++) 
        {
            var option = document.createElement("option");
            option.text = this.arrTimezone[count].name;
            option.value = this.arrTimezone[count].value;
            select.appendChild(option);
        }
        span.appendChild(select);
        var btnClose = document.createElement("button");
        btnClose.setAttribute("class","btn btn-sm btn-success pull-right");
        btnClose.addEventListener("click",function(){
            self.calFooterTimeZone.closeCalendar();
        });
        btnClose.innerHTML = "Close";
        divFooter.appendChild(span);
        divFooter.appendChild(btnClose);
        
        return divFooter;
    }
}