    private waitForInstanceCallback(reactComponent: DynamicComponentService,runningTime: Number):boolean
    {
        if (runningTime >= this.MAX_COMPONENT_CREATION_TIME) 
        {
            console.error(`NSGridReact: React Component '${reactComponent.getReactComponentName()}' not created within ${this.MAX_COMPONENT_CREATION_TIME}ms`);
            return false;
        }
        return true;
    };
    
    private batchUpdateCallback()
    {
        if(this.updateCallbacksOnUpdate.length > 0)
        {
            for (let item of this.updateCallbacksOnUpdate)
            {
                if(item && item.dynamicCompRef && item.dynamicCompRef.getComponentInstance())
                {
                    item.callback(item.dynamicCompRef,item.dynamicCompRef.getComponentInstance(),item.container);
                }
            }
            this.updateCallbacksOnUpdate = [];
        }
    };
    
